import React, { useState } from 'react'
import "../../Styles/pages/CheckoutPage.css"
import { useGlobal } from '../context/GlobalContext';
import { navigate } from 'gatsby';

function CheckoutPage() {
  const {state }  = useGlobal();
  console.log(state, "CheckoutPage state");
  const [formData, setFormData] = useState({
    firstName: '',
    lastName: '',
    country: 'Country/Region',
    state: '',
    city: '',
    zipCode: '',
    address: '',
    apartment: '',
    phone: '',
    email: '',
    createAccount: false,
    shipToDifferent: false,
    orderNotes: '',
    shippingMethod: 'free',
    paymentMethod: 'bank'
  });

  const [showCoupon, setShowCoupon] = useState(false);
  const [couponCode, setCouponCode] = useState('');

  const handleInputChange = (e) => {
    const { name, value, type, checked } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: type === 'checkbox' ? checked : value
    }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log('Order submitted:', formData);
    navigate('/ordersuccess');};

  return (
    <div className="checkout-container">
      <header className="checkout-header container">
        <h1>Checkout</h1>
        <nav className="breadcrumb">
          <span className="home-link">Home</span>
          <span className="separator">›</span>
          <span className="current">Checkout</span>
        </nav>
      </header>

      <div className="checkout-content container">
        <div className="checkout-form">
          <div className="coupon-section">
            <p>
              Have a coupon?{' '}
              <button 
                type="button" 
                className="coupon-toggle"
                onClick={() => setShowCoupon(!showCoupon)}
              >
                Click here to enter your code
              </button>
            </p>
            {showCoupon && (
              <div className="coupon-form">
                <input
                  type="text"
                  placeholder="Coupon code"
                  value={couponCode}
                  onChange={(e) => setCouponCode(e.target.value)}
                />
                <button type="button" className="apply-coupon">Apply coupon</button>
              </div>
            )}
          </div>

          <form onSubmit={handleSubmit} className="billing-form">
            <div className="form-row">
              <div className="form-group">
                <input
                  type="text"
                  name="firstName"
                  placeholder="First name**"
                  value={formData.firstName}
                  onChange={handleInputChange}
                  required
                />
              </div>
              <div className="form-group">
                <input
                  type="text"
                  name="lastName"
                  placeholder="Last name**"
                  value={formData.lastName}
                  onChange={handleInputChange}
                  required
                />
              </div>
            </div>

            <div className="form-row">
              <div className="form-group">
                <select
                  name="country"
                  value={formData.country}
                  onChange={handleInputChange}
                  className="select-field"
                >
                  <option value="Country/Region">Country/Region</option>
                  <option value="US">United States</option>
                  <option value="CA">Canada</option>
                  <option value="UK">United Kingdom</option>
                  <option value="AU">Australia</option>
                </select>
              </div>
              <div className="form-group">
                <input
                  type="text"
                  name="state"
                  placeholder="State**"
                  value={formData.state}
                  onChange={handleInputChange}
                  required
                />
              </div>
            </div>

            <div className="form-row">
              <div className="form-group">
                <input
                  type="text"
                  name="city"
                  placeholder="Town/City**"
                  value={formData.city}
                  onChange={handleInputChange}
                  required
                />
              </div>
              <div className="form-group">
                <input
                  type="text"
                  name="zipCode"
                  placeholder="Zip Code**"
                  value={formData.zipCode}
                  onChange={handleInputChange}
                  required
                />
              </div>
            </div>

            <div className="form-row">
              <div className="form-group">
                <input
                  type="text"
                  name="address"
                  placeholder="Street Address**"
                  value={formData.address}
                  onChange={handleInputChange}
                  required
                />
              </div>
              <div className="form-group">
                <input
                  type="text"
                  name="apartment"
                  placeholder="Apartments, suit, unit, etc (Optional)"
                  value={formData.apartment}
                  onChange={handleInputChange}
                />
              </div>
            </div>

            <div className="form-row">
              <div className="form-group">
                <input
                  type="tel"
                  name="phone"
                  placeholder="Phone Number**"
                  value={formData.phone}
                  onChange={handleInputChange}
                  required
                />
              </div>
              <div className="form-group">
                <input
                  type="email"
                  name="email"
                  placeholder="Email Address**"
                  value={formData.email}
                  onChange={handleInputChange}
                  required
                />
              </div>
            </div>

            <div className="checkbox-row">
              <label className="checkbox-label">
                <input
                  type="checkbox"
                  name="createAccount"
                  checked={formData.createAccount}
                  onChange={handleInputChange}
                />
                <span className="checkmark"></span>
                Create an account?
              </label>
              <label className="checkbox-label">
                <input
                  type="checkbox"
                  name="shipToDifferent"
                  checked={formData.shipToDifferent}
                  onChange={handleInputChange}
                />
                <span className="checkmark"></span>
                Ship to a different address?
              </label>
            </div>

            <div className="form-group full-width">
              <textarea
                name="orderNotes"
                value={formData.orderNotes}
                onChange={handleInputChange}
                rows="4"
                placeholder="Order notes (optional)"
              ></textarea>
            </div>
          </form>
        </div>

        <div className="order-summary">
          <div className="summary-header">
            <span className="product-title">Product</span>
            <span className="subtotal-title">Subtotal</span>
          </div>
          
        
          
          {
            state.cart.map((item) => (
              <div className="summary-item" key={item.id}>
                <span className="product-name">{item.name} × {item.quantity}</span>
                <span className="product-price">${(item.price * item.quantity).toFixed(2)}</span>
              </div>
            ))
          }
          {
            state.cart.length === 0 && (
              <div className="summary-item">
                <span className="product-name">No Products in Cart</span>
                <span className="product-price">$0.00</span>
              </div>
            )
          }

          <div className="summary-row subtotal-row">
            <span className="subtotal-label">Subtotal</span>
            <span className="subtotal-amount">${state?.checkoutDetails?.orderSummary?.subtotal}</span>
          </div>

          <div className="shipping-section">
            <h4 className="shipping-title">Shipping</h4>
            <div className="shipping-options">
              <label className="radio-label">
                <input
                  type="radio"
                  name="shippingMethod"
                  value="flat"
                  checked
               
                />
                <span className="radio-custom"></span>
                {
                  state?.checkoutDetails?.orderSummary?.shippingCost === 10 ? `Flat rate: $${state?.checkoutDetails?.orderSummary?.shippingCost || 0}`:  `Free shipping: $${state?.checkoutDetails?.orderSummary?.shippingCost || 0}`
                }
              </label>
         
            </div>
          </div>

          <div className="summary-row total-row">
            <span className="total-label">Total</span>
            <span className="total-amount">${state?.checkoutDetails?.orderSummary?.total}</span>
          </div>

          <div className="payment-section">
            <div className="payment-options">
              <label className="radio-label">
                <input
                  type="radio"
                  name="paymentMethod"
                  value="bank"
                  checked={formData.paymentMethod === 'bank'}
                  onChange={handleInputChange}
                />
                <span className="radio-custom"></span>
                Direct bank transfer
              </label>
              <label className="radio-label">
                <input
                  type="radio"
                  name="paymentMethod"
                  value="cash"
                  checked={formData.paymentMethod === 'cash'}
                  onChange={handleInputChange}
                />
                <span className="radio-custom"></span>
                Cash on delivery
              </label>
              <label className="radio-label">
                <input
                  type="radio"
                  name="paymentMethod"
                  value="paypal"
                  checked={formData.paymentMethod === 'paypal'}
                  onChange={handleInputChange}
                />
                <span className="radio-custom"></span>
                PayPal <span className="paypal-info">What is PayPal?</span>
              </label>
            </div>
          </div>

          <button type="submit" className="place-order-btn" onClick={handleSubmit}>
            Place Order
          </button>
        </div>
      </div>
    </div>
  );
}

export default CheckoutPage
